<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">جدول المصروفات</h4>
                      <div class="page-title-right">
                          <button type="button" @click="$refs.FormModal.create()" class="btn btn-success waves-effect waves-light">
                              + إضافة جديد
                          </button>
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row">
              <div class="col-12">
                  <div class="card">
                      <div v-if="payment_types&&payment_types.data.length" class="card-body">
                          <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                              <thead>
                              <tr>
                                  <th>نوع المصروف</th>
                                  <th>مصروف اليوم</th>
                                  <th>مصروف الأسبوع</th>
                                  <th>مصروف الشهر</th>
                                  <th>مصروف السنة</th>
                                  <th>خيارات</th>
                              </tr>
                              </thead>
                              <tbody>
                              <tr v-for="(payment_type,index) in payment_types.data">
                                  <td>{{payment_type.name}}</td>
                                  <td>{{moneyFormat(payment_type.total_payments['day'],0)}} L.E</td>
                                  <td>{{moneyFormat(payment_type.total_payments['week'],0)}} L.E</td>
                                  <td>{{moneyFormat(payment_type.total_payments['month'],0)}} L.E</td>
                                  <td>{{moneyFormat(payment_type.total_payments['year'],0)}} L.E</td>
                                  <td>
                                    <div class="btn-group">
                                        <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                          المزيد <i class="mdi mdi-chevron-down"></i>
                                        </button>
                                        <div class="dropdown-menu" style="min-width: 120px;">
                                            <a @click="$refs.FormModal.edit(payment_type)" class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-edit"></i> تعديل
                                            </a>
                                            <a @click="$refs.FormModal.destroy(payment_type.id)" class="dropdown-item dropdownItem" href="javascript:;">
                                              <i class="far fa-trash-alt"></i> حذف
                                            </a>
                                        </div>
                                    </div>
                                  </td>
                              </tr>
                              </tbody>
                          </table>
                          <pagination-nav v-if="payment_types.last_page>1" :pageSize="5" :currentPage="2" :GoToPage="GoToPage" />
                      </div>
                      <div v-else class="card-body">
                          <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                          <p v-else class="card-title-desc" style="text-align: center;">
                            لا يوجد بيانات مضافة حتى الآن
                          </p>
                      </div>
                  </div>
              </div> <!-- end col -->
          </div> <!-- end row -->
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/payment/add';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
        return {
            payment_types:null,loader:false
        }
    },  
  created() { 
    this.fetchData();
  },
  methods: {
    fetchData(num=1) {
      this.payment_types=null;
      this.loader=true;
      let data = {params: {page:num}};
      axios.get('/api/payment_types',data).then(({data}) => {
          this.payment_types=data.success.payment_types;
          if(!data.success.payment_types.data.length&&num!=1){
              this.fetchData(1)
          }           
          this.loader=false;
      });  
    },
  },
}
</script>